import React from "react";
import ImageGuitar from "./assets/Guitar.png";
import Button from "./Button";
import ContentInfo from "./ContentInfo";

function Content(props) {
  return (
    <>
      <div className="content">
        <img src={ImageGuitar} alt=""></img>
        <ContentInfo
          productedition="Exclusive Edition"
          titleproduct="Guitar Signature - Henrique Gonçalves"
          description="This guitar is made with material recycled and tested by Henrique Gonçalves(#HG) a of most bigger guitar player of world."
          imageproduct={ImageGuitar}
          price="R$ 2.000"
        ></ContentInfo>
      </div>
    </>
  );
}

export default Content;
