import React from "react";
import Button from "./Button";

function ContentInfo(props) {
  return (
    <>
      <div className="content-info">
        <span>{props.productedition}</span>
        <h1>{props.titleproduct}</h1>
        <h3>Description</h3>
        <p>{props.description}</p>
        <div className="footer-content">
          <h2>{props.price}</h2>
          <Button label="Add to cart"></Button>

          <svg
            width="158"
            height="625"
            viewBox="0 0 158 625"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              id="path"
              d="M82 624.5C82 624.5 175.5 527 153.5 309C131.5 91 1 0.5 1 0.5"
              stroke="black"
            />
          </svg>
        </div>
      </div>
    </>
  );
}

export default ContentInfo;
